import { StaffService } from '@miq/utils';

class SiteSettingsStaffService extends StaffService {
  constructor(path) {
    super(path);
  }

  patchSite(settingSlug, values, oldValues) {
    return this.patchPath(`${this.path}${settingSlug}/site/`, values, oldValues);
  }
}

export const siteServices = new SiteSettingsStaffService('settings/');
