import React from 'react';

import { componentToHtml } from '@miq/editor';
import { StaffCoverUpdateForm } from '@miq/adminjs/';
import Form, { useForm } from '@miq/form';
import { Picture, Button } from '@miq/components';

import { AdminView } from '../views';
import { siteServices } from './utils';

export const ClosePageRender = (props) => {
  const { ct_title, ct_text, ct_image_data } = props;
  const hasText = ct_title || ct_text;

  return (
    <div className="close-page">
      {ct_image_data && <Picture {...ct_image_data} />}

      {hasText && (
        <div className="close-page-inner">
          <div className="text-center p-3">
            {ct_title && <h1 className="close-template-title mb-1">{ct_title}</h1>}
            {ct_text && <h3 className="close-template-text">{ct_text}</h3>}
          </div>
        </div>
      )}
    </div>
  );
};

const ClosePageForm = ({ setting = {}, ...props }) => {
  const { toast, setSetting } = props;
  const form = useForm({ ct_title: setting.ct_title || '', ct_text: setting.ct_text || '' });
  const { slug } = setting;

  const handleSubmit = (e) => {
    e.preventDefault();
    const ct_html = componentToHtml(<ClosePageRender {...{ ...setting, ...form.values }} />);

    return siteServices
      .patch(slug, { ...form.values, ct_html })
      .then(({ isUpdated, ...settingData }) => {
        if (isUpdated) {
          setSetting && setSetting(settingData);
          toast && toast.success({ message: 'Page updated.' });
        }
      })
      .catch((err) => form.handleError(err));
  };

  const handleImageCreate = (imgData) => {
    const data = { ...setting, ct_image_data: imgData };

    const html = componentToHtml(<ClosePageRender {...data} />);
    return siteServices.patch(slug, { ct_image: imgData.slug, ct_html: html }).then(({ isUpdated, ...res }) => {
      if (!isUpdated) return;
      setSetting(res);
      toast?.success({ message: 'Image added.' });
    });
  };

  const handleImageUpdate = ({ isUpdated, ...imgData }) => {
    const data = { ...setting, ct_image_data: imgData };
    const html = componentToHtml(<ClosePageRender {...data} />);
    return siteServices.patch(slug, { ct_html: html }).then(({ isUpdated, ...res }) => {
      if (!isUpdated) return;
      setSetting(res);
      toast?.success({ message: 'Image updated.' });
    });
  };

  return (
    <Form context={form} onSubmit={handleSubmit} className="setting-close-page-form">
      <div className="d-grid grid-md-5 gap-2">
        <div className="span-md-3">
          <div className="mb-1">
            <Form.Label value="Title" />
            <Form.TextInput name="ct_title" placeholder="Write title ..." error={form.errors.ct_title} maxLength={50} />
          </div>

          <div className="mb-1">
            <Form.Label value="Text" />
            <Form.TextAreaX name="ct_text" placeholder="Write text ..." error={form.errors.ct_text} maxLength={99} />
          </div>

          <div className="mb-2">
            <Form.Submit value="Save" className="btn btn-primary-2" />
          </div>
        </div>

        <div className="span-md-2">
          <StaffCoverUpdateForm
            slug={setting?.ct_image}
            data={setting?.ct_image_data}
            onCreate={handleImageCreate}
            onUpdate={handleImageUpdate}
            onDelete={() => {
              const data = { ...setting, ct_image_data: null, ct_image: null };
              const html = componentToHtml(<ClosePageRender {...data} />);
              return siteServices.patch(slug, { ct_html: html }).then(() => {
                setSetting(data);
                toast?.success({ message: 'Image deleted.' });
              });
            }}
            className="mb-1"
          />
        </div>
      </div>
    </Form>
  );
};

export default function SiteLiveSettingForm({ setting = {}, ...props }) {
  const { toast, setSetting } = props;

  if (!setting?.slug) return null;

  const text = setting.is_live
    ? 'Your site is published.'
    : 'Your site is not published. No one but you can see your content.\nCustomize your landing.';

  return (
    <AdminView.Section
      title="Site Status"
      text={text}
      actions={
        <Button
          onClick={() => {
            return siteServices
              .patch(setting.slug, { is_live: !setting.is_live })
              .then(({ isUpdated, ...data }) => {
                if (!isUpdated) return;

                setSetting?.(data);
                toast?.success({
                  message: data.is_live ? 'Site published.' : 'No one but you can see the content.',
                });
              })
              .catch((err) => {});
          }}
          className={setting.is_live ? 'btn-danger-3' : 'btn-primary'}
        >
          {setting && setting.is_live ? 'Unpublish site' : 'Publish site'}
        </Button>
      }
    >
      {!setting.is_live && <ClosePageForm {...props} setting={setting} />}
    </AdminView.Section>
  );
}
