import { getClassName } from '@miq/utils';

import './components.scss';

export const PublishedStatusSpan = ({ is_published, pill }) => {
  return (
    <span className={getClassName(['published-status-span', pill && 'pill', is_published ? 'published' : 'draft'])}>
      {is_published ? 'Published' : 'Draft'}
    </span>
  );
};
