import { getClassName } from '@miq/utils';
import ViewSection from './ViewSection';
import ViewHeader from './ViewHeader';
import { AdminNavLink } from '../Layout/components';

import './views.scss';

const ViewTabNavLinks = (props) => {
  const { urls, ...rest } = props;

  return (
    <div className={getClassName(['admin-view-tab-navlinks', 'd-flex', rest.className])}>
      {urls?.map((data) => (
        <AdminNavLink
          {...data}
          exact={data.exact}
          to={data.to}
          label={data.label}
          key={data.to}
          className={getClassName(['admin-view-tab-navlink', 'me-1', data.className])}
        />
      ))}
    </div>
  );
};

/**
 *
 * @param {*} param0
 * @returns
 */

export const AdminView = ({ children, ...props }) => {
  const { title, actions, back, ...rest } = props;

  return (
    <div {...rest} className={getClassName(['miq-view', props.className])}>
      <ViewHeader {...{ title, actions, back }} className="miq-view-header" />

      {children}
    </div>
  );
};

AdminView.Section = ViewSection;
AdminView.TabNavLinks = ViewTabNavLinks;
